export declare type QosEvent = any; // tslint:disable-line

/**
 * @internal
 */
interface IQosMonitor {
  qos: QosEvent;
  writeSuccess(): void;
  /* tslint:disable:no-any */
  writeUnexpectedFailure(tagName?: string, ex?: Error, extraData?: { [key: string]: any }): void;
  writeExpectedFailure(tagName?: string, ex?: Error, extraData?: { [key: string]: any }): void;
  /* tslint:enable:no-any */
}

export default IQosMonitor;
